___CSS_LOADER_EXPORT___.push([module.id, `/* Positional styling */
.home-div {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    gap: 1rem;
    width: clamp(300px, 80%, 800px);

    padding: 2rem;

    border-radius: 10px;
    margin-bottom: 2rem;
}

.home-div section {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    gap: 1rem;
    text-align: center;
}

/* Home container visual styling */
.home-div {
    background-color: var(--mediumForest);
}

.home-div img {
    aspect-ratio: 1;
    width: 80%;
    object-fit: cover;
    box-shadow: 2px 2px 5px black;
}

.home-div p {
    text-align: justify;
    max-width: 60ch;
}`, "",{"version":3,"sources":["webpack://./src/styles/pages/homeStyles.css"],"names":[],"mappings":"AAAA,uBAAuB;AACvB;IACI,aAAa;IACb,wBAAwB;IACxB,mBAAmB;IACnB,SAAS;IACT,+BAA+B;;IAE/B,aAAa;;IAEb,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,wBAAwB;IACxB,mBAAmB;IACnB,SAAS;IACT,kBAAkB;AACtB;;AAEA,kCAAkC;AAClC;IACI,qCAAqC;AACzC;;AAEA;IACI,eAAe;IACf,UAAU;IACV,iBAAiB;IACjB,6BAA6B;AACjC;;AAEA;IACI,mBAAmB;IACnB,eAAe;AACnB","sourcesContent":["/* Positional styling */\n.home-div {\n    display: flex;\n    flex-flow: column nowrap;\n    align-items: center;\n    gap: 1rem;\n    width: clamp(300px, 80%, 800px);\n\n    padding: 2rem;\n\n    border-radius: 10px;\n    margin-bottom: 2rem;\n}\n\n.home-div section {\n    display: flex;\n    flex-flow: column nowrap;\n    align-items: center;\n    gap: 1rem;\n    text-align: center;\n}\n\n/* Home container visual styling */\n.home-div {\n    background-color: var(--mediumForest);\n}\n\n.home-div img {\n    aspect-ratio: 1;\n    width: 80%;\n    object-fit: cover;\n    box-shadow: 2px 2px 5px black;\n}\n\n.home-div p {\n    text-align: justify;\n    max-width: 60ch;\n}"],"sourceRoot":""}]);
