*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    appearance: none;
}

:root {
    --forestGreen: #1e2419;
    --offWhite: #f0f0f0;
    --accentBrass: #c9a96e;
    --mediumForest: #2a3021;

    font-family: 'fira_sansregular', system-ui, -apple-system, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

html {
    color: var(--offWhite);
    font-size: max(16px, 1rem);
    line-height: 1.6;
}

button {
    color:inherit;
    padding: 0.6rem 0.4rem;

    border: 2px solid var(--accentBrass);
    border-radius: 10px;

    background-color: var(--mediumForest);
    font-size: 1.2rem;
    transition: 0.2s;
}

button:active {
    background-color: olive;
    transition: 0.2s;
}

img {
    border-radius: 10px;
    border: 2px solid var(--accentBrass);
}

body {
    background-color: var(--forestGreen);
}

body, #content {
    min-width: 100%;
}

header {
    margin-top: 1rem;
}

/* flex box styling for body */
body, #content {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;

    gap: 1rem;
