.menu-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    width: max(300px, 60%);
    padding: 1rem;
    gap: 0.5rem;
}

.menu-container > article {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    text-align: center;
    padding: 1rem;
}

/* Visual styling */
.menu-container {
    background-color: var(--mediumForest);
    border-radius: 10px;
}

.menu-container > article {
    border: 1px solid var(--offWhite);
    border-radius: 10px;
}`, "",{"version":3,"sources":["webpack://./src/styles/pages/menuStyles.css"],"names":[],"mappings":"AAAA,uBAAuB;AACvB;IACI,aAAa;IACb,2DAA2D;IAC3D,sBAAsB;IACtB,aAAa;IACb,WAAW;AACf;;AAEA;IACI,aAAa;IACb,wBAAwB;IACxB,mBAAmB;IACnB,kBAAkB;IAClB,aAAa;AACjB;;AAEA,mBAAmB;AACnB;IACI,qCAAqC;IACrC,mBAAmB;AACvB;;AAEA;IACI,iCAAiC;IACjC,mBAAmB;AACvB","sourcesContent":["/* Positional Styling */\n.menu-container {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\n    width: max(300px, 60%);\n    padding: 1rem;\n    gap: 0.5rem;\n}\n\n.menu-container > article {\n    display: flex;\n    flex-flow: column nowrap;\n    align-items: center;\n    text-align: center;\n    padding: 1rem;\n}\n\n/* Visual styling */\n.menu-container {\n    background-color: var(--mediumForest);\n    border-radius: 10px;\n}\n\n.menu-container > article {\n    border: 1px solid var(--offWhite);\n    border-radius: 10px;\n}"],"sourceRoot":""}]);
